import {getAlbum} from '@/api/homeApi';

export default {
    name: "Album",
    data() {
      return {
          album: [], // 专辑
          banner: [], // 轮播图
          params: {
              limit: 30,
              skip: 0,
              order: 'new'
          },
          hasMore: true
      }
    },
    mounted() {
        uni.setNavigationBarTitle({title: '专辑'});
        this.getAlbumData();
    },
    methods: {
        getAlbumData() {
            getAlbum(this.params).then(res => {
                let data = res.data.res;

                if (this.banner.length === 0) {
                    this.banner = data.banner;
                }

                if (data.album.length === 0) {
                    this.hasMore = false;
                    uni.showToast({
                        title: "没有更多数据了",
                        icon: "none"
                    });
                    return;
                }

                this.album = [...this.album, ...data.album];
                // console.log(this.album);
            })
        },
        handleToBottom() {
            if (this.hasMore) {
                this.params.skip += this.params.limit;
                this.getAlbumData();
            } else {
                uni.showToast({
                    title: '没有更多数据啦',
                    icon: 'none'
                })
            }
        }
    }
}
